@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap');
@import "./style-config/mixins.scss", "./style-config/variable.scss";

* {
    margin: 0;
    padding: 0;
    font-family: 'DM Sans', sans-serif;
}

p {
    @include typography(14px, 400, 20px); //for small screen
    @include small-screen{
        @include typography(18px, 400, 24px);// for medium and large screen
    }
}

h2 {
    @include typography(32px, 700, 1.2);
    @include small-screen{
        @include typography(47px, 700, 1.2);
    }
}

h3 {
    @include typography(14px, 500, 1.2);
    @include small-screen{
        @include typography(26px, 500, 1.2);
    }
}

h4 {
    @include typography(16px, 500, 1.2);
    @include small-screen{
        @include typography(22px, 500, 1.2);
    }
}