@import "../../../style-config/mixins.scss",
  "../../../style-config/variable.scss";

.project-show-item {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-left: -10px;
  margin-right: -10px;
  // @include small-screen{
  //   height: 570px;
  // }
  .project-item {
    width: 155px;
    height: 150px;
    overflow: hidden;
    margin: 10px;
    position: relative;
    cursor: pointer;
    @include transition;
    @include small-screen{
      width: 265px;
      height: 265px;
      overflow: hidden;
      border-radius: 16px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      display: block;
    }
    .meta-content {
      visibility: visible;
      opacity: 1;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 10px;
      width: 100%;
      border-radius: 10px;
      @include glassmorphism-effect;
      @include transition;
      @include small-screen{
        width: fit-content;
        visibility: hidden;
        margin-right: 20px;
        border-radius: 10px;
        left: 20px;
        padding: 10px;
      }
      h3 {
        color: $black;
        font-weight: 400;
        margin-bottom: 10px;
      }
      .go-to-cta {
        display: flex;
        align-items: center;
        .tex {
          @include typography(10px, 400, 10px);
        }
        .arrow-icon {
          width: 15px;
          height: 15px;
          margin-left: 5px;
        }
      }
    }
    &.zoomIn{
        transform: scale(0);
        animation: zoomIn 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forward;
    }
    &.zoomOut{
        transform: scale(1);
        animation: zoomOut 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275) forward;
    }
    &:hover {
     @include small-screen{
      z-index: 1;
      transform: scale(1.2);
      box-shadow: 0 0 33px -8px rgba(0, 0, 0, 0.75);
      .meta-content {
        visibility: visible;
        opacity: 1;
        transform: translateY(-20px);
      }
     }
    }
  }
}
@keyframes zoomIn{
    0%{
        transform: scale(0);
    }
    100%{
        transform: scale(1);
    }
}
@keyframes zoomOut{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(0);
    }
}
